"use strict";

import { placeLineX } from "../../../js/import/functions";

if (document.querySelector(".fitness") || document.querySelector(".salon")) {

  let service_gallery_slider_icon = document.querySelectorAll(".service-row .slider__icon");
  service_gallery_slider_icon.forEach(function (icon) {
    icon.addEventListener("click", function (event) {
      event.preventDefault();
      let prev_icon = document.querySelector(".service-row .slider__icon--active");
      let service_gallery_slider_item = document.querySelector(".service-gallery-slider");
      let service_gallery_slider_row = document.querySelector(".service-gallery-slider-row");
      document.querySelector(".service-gallery-slider-controls").classList.toggle("service-gallery-slider-controls--module");
      if (service_gallery_slider_item) {
        service_gallery_slider_item.classList.remove("service-gallery-slider");
        service_gallery_slider_item.classList.add("service-gallery-slider-row");
      } else if (service_gallery_slider_row) {
        service_gallery_slider_row.classList.remove("service-gallery-slider-row");
        service_gallery_slider_row.classList.add("service-gallery-slider");
      }

      icon.classList.toggle("slider__icon--active");
      prev_icon.classList.toggle("slider__icon--active");
    });
  });
}

if (document.querySelector(".service-price")) {
  let service_price_list_items = document.querySelectorAll(".service-price-list__item");
  let service_price_underline =  document.querySelector('.service-price .underline');
  let service_price_item_active = document.querySelector(".service-price-list__item--active");
  placeLineX(service_price_underline, {'width': service_price_item_active.clientWidth + 'px','left': service_price_item_active.offsetLeft + 'px',});

  service_price_list_items.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(service_price_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_item = document.querySelector(".service-price-preview-list__item--active");
      let prev_link = document.querySelector(".service-price-list__item--active");
      let item = document.querySelector(link.getAttribute("href"));

      item.classList.toggle("service-price-preview-list__item--active");
      prev_item.classList.toggle("service-price-preview-list__item--active");
      prev_link.classList.toggle("service-price-list__item--active");
      link.classList.toggle("service-price-list__item--active");
    });
  });
}